import { JobStatus, JobsSet, ThemaCategoriesResponse, JobsSetDetails, StartInferenceResult } from './types';
import config from './config';
import { useState, useEffect } from 'react';

export const pollJobsStatus = (
  jobIds: string[],
  sessionId: string,
  setJobStatuses: React.Dispatch<React.SetStateAction<JobStatus[]>>
) => {
  const interval = setInterval(async () => {
    try {
      const response = await fetch(config.jobsStatusUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-Session-Id': sessionId,
        },
        body: JSON.stringify({ JobsIds: jobIds }),
      });
      const data = await response.json();
      setJobStatuses(data.jobsStatus);

      // Sprawdź, czy wszystkie joby zakończone (np. status === 'Completed')
      if (data.jobsStatus.every((job: any) => job.status === 'Completed')) {
        clearInterval(interval);
        // Tutaj możesz pobrać wyniki lub zakończyć polling
      }
    } catch (error) {
      console.error('Error polling job statuses:', error);
    }
  }, 2000);
}; 

export const fetchJobsSets = async (sessionId: string): Promise<JobsSet[]> => {
  try {
    const response = await fetch(`${config.themaCategoriesUrl}`, {
      method: 'GET',
      headers: {
        'X-Session-Id': sessionId,
      },
    });
    const data: ThemaCategoriesResponse = await response.json();
    return data.jobsSets;
  } catch (error) {
    console.error('Error fetching jobs sets:', error);
    return [];
  }
};

export const fetchJobsSetDetails = async (jobSetId: string, sessionId: string): Promise<JobsSetDetails | null> => {
  try {
    const response = await fetch(`${config.jobsSetDetailsUrl}/${jobSetId}`, {
      method: 'GET',
      headers: {
        'X-Session-Id': sessionId,
      },
    });
    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Error fetching job details:', error);
    return null;
  }
};

export const startInference = async (description: string, sessionId: string): Promise<StartInferenceResult> => {
  const response = await fetch(config.apiUrl, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      'X-Session-Id': sessionId,
    },
    body: JSON.stringify({ description: description })  ,
  });
  
  if (!response.ok) {
    throw new Error(`HTTP error! status: ${response.status}`);
  }
  
  return await response.json();
};