import React, { useState, useEffect } from 'react';
import './App.css';
import { JobStatus, JobsSet, ThemaCategoriesResponse, ThemaCategory, JobsSetDetails, StartInferenceResult } from './types';
import config from './config';
import { pollJobsStatus, fetchJobsSets, fetchJobsSetDetails, startInference } from './api';

function getOrCreateSessionId() {
  let sessionId = localStorage.getItem('sessionId');
  if (!sessionId) {
    sessionId = crypto.randomUUID();
    localStorage.setItem('sessionId', sessionId);
  }
  return sessionId;
}
const sessionId = getOrCreateSessionId();

function App() {
  const [jobsSets, setJobsSets] = useState<JobsSet[]>([]);
  const loadJobsSets = async () => {
    const jobs = await fetchJobsSets(sessionId);
    setJobsSets(jobs);
  };
  useEffect(() => {
    loadJobsSets();
  }, []);

  const [bookDescription, setBookDescription] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [progress, setProgress] = useState(0);
  const [results, setResults] = useState<ThemaCategory[]>([]);
  const [jobIds, setJobIds] = useState<string[]>([]);
  const [jobStatuses, setJobStatuses] = useState<JobStatus[]>([]);    
  const [selectedJobSetId, setSelectedJobSetId] = useState<string | null>(null);
  const [jobDetails, setJobDetails] = useState<JobsSetDetails | null>(null);
  const [showNewForm, setShowNewForm] = useState(false);

  const handleJobsSetClick = async (jobsSetId: string) => {
    setSelectedJobSetId(jobsSetId);
    setShowNewForm(false);
    const details = await fetchJobsSetDetails(jobsSetId, sessionId);
    setJobDetails(details);
  };

  const handleNewJobsSetClick = () => {
    setSelectedJobSetId(null);
    setJobDetails(null);
    setShowNewForm(true);
    setBookDescription('');
    setResults([]);
    setJobStatuses([]);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setProgress(0);
    setResults([]);
    setJobStatuses([]);
  
    try {
      const data = await startInference(bookDescription, sessionId);
      const newJobId = data.inferenceData.createdJobsSetId;
      
      // Odśwież listę jobów
      await loadJobsSets();
      
      // Przejdź do szczegółów nowo utworzonego joba
      setShowNewForm(false);
      setSelectedJobSetId(newJobId);
      
      // Pobierz szczegóły nowego joba
      const details = await fetchJobsSetDetails(newJobId, sessionId);
      setJobDetails(details);
      
    } catch (error) {
      console.error('Error:', error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>THEMA Book Categorization</h1>
      </header>
      <main className="App-main">
        <div className="app-layout">
          {/* Lewa kolumna - lista jobów */}
          <div className="jobs-list-column">
            <h3>Jobs in Session</h3>
            <ul className="jobs-list">
              {/* Element "New" */}
              <li 
                className={`job-item new-job-item ${showNewForm ? 'active' : ''}`}
                onClick={handleNewJobsSetClick}
              >
                <div className="job-id">➕ New Job</div>
                <div className="job-date">Create new categorization</div>
              </li>
              
              {/* Istniejące joby */}
              {jobsSets.map(jobsSet => (
                <li 
                  key={jobsSet.id} 
                  className={`job-item ${selectedJobSetId === jobsSet.id ? 'active' : ''}`}
                  onClick={() => handleJobsSetClick(jobsSet.id)}
                >
                  <div className="job-id">{jobsSet.id}</div>
                  <div className="job-date">
                    {new Date(jobsSet.createdDate).toLocaleString()}
                  </div>
                </li>
              ))}
            </ul>
          </div>

          {/* Prawa kolumna */}
          <div className="main-content-column">
            {showNewForm && (
              <>
                <form onSubmit={handleSubmit} className="categorization-form">
                  <div className="form-group">
                    <label htmlFor="bookDescription">Book Description:</label>
                    <textarea
                      id="bookDescription"
                      value={bookDescription}
                      onChange={(e) => setBookDescription(e.target.value)}
                      placeholder="Enter book description..."
                      rows={6}
                      required
                    />
                  </div>
                  <button type="submit" disabled={isLoading}>
                    {isLoading ? 'Categorizing...' : 'Categorize Book'}
                  </button>
                </form>

                {isLoading && (
                  <div className="progress-container">
                    <div className="progress-bar">
                      <div 
                        className="progress-fill"
                        style={{ width: `${progress}%` }}
                      />
                    </div>
                    <p>Processing: {progress}%</p>
                  </div>
                )}

                {jobStatuses.length > 0 && (
                  <div className="jobs-status-container">
                    <h3>Statusy zadań:</h3>
                    <ul>
                      {jobStatuses.map(job => (
                        <li key={job.jobId}>
                          <strong>{job.jobId}:</strong> {job.status}
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </>
            )}

            {jobDetails && (
              <div className="job-details-container">
                <h3>Job Details: {jobDetails.id}</h3>
                <div className="job-info">
                  <p><strong>Description:</strong> {jobDetails.description}</p>
                  <p><strong>Status:</strong> {jobDetails.status}</p>
                  <p><strong>Created:</strong> {new Date(jobDetails.createdDate).toLocaleString()}</p>
                </div>
                
                {jobDetails.categories && jobDetails.categories.length > 0 && (
                  <div className="results-container">
                    <h4>Categorization Results:</h4>
                    <ul className="results-list">
                      {jobDetails.categories.map((result, index) => (
                        <li key={index}>
                          <span className="category">{result.category}</span>
                          <span className="probability">
                            {(result.probability * 100).toFixed(2)}%
                          </span>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      </main>
    </div>
  );
}

export default App; 